import React , {Fragment} from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../actions/auth';
 
//Importing Scss
import '../sass/components/_mainsection.scss';

const MainSection = ({ logout, isAuthenticated }) =>{

    const guestLinks = () =>(
        <Fragment>
            <li className="nav-item">
                <Link to="/login">
                    Login
                </Link>
            </li>
            <li className="nav-item">    
                <Link to="/signup">
                    Singup
                </Link>    
            </li>
        </Fragment>
    );

    const authLinks = () =>(
            <li className="nav-item">    
                <a href="" onClick={logout}>Logout</a>  
            </li>
    );

    return(
        <>
        <div className="row p-0 m-0 main-section">
            <div className="p-0 m-0 col-sm-12 search-bar login">
                <div className=" row my-4 mx-4">
                    <div className="p-0 m-0 col-sm-12 col-md-10 col-lg-10 col-xl-10">
                        <input className="col-sm-12 col-md-6 col-lg-6 col-xl-6 text-center m-0 p-0 searchterm" placeholder="Search" classtype="text" name="Search" id="search"/>
                    </div>
                    <div className="p-0 m-0 col-sm-12 col-md-2 col-lg-2 col-xl-2 Profile d-flex flex-row justify-content-around align-items-center">
                        {/* <li className="nav-item">
                            <Link to="/login">
                                <a href="" className="loginbtn">Login</a>
                            </Link>
                        </li>
                        <li className="nav-item">    
                            <Link to="/singup">
                                <a href="">Singup</a>
                            </Link>    
                        </li> */}
                        {isAuthenticated ? authLinks() : guestLinks()}   
                    </div>
                </div>
            </div>
            <div className="m-0 col-sm-12 col-md-12 col-lg-12 col-xl-12 song-content">
                <div className="row">
                    <div className=" col-sm-12 col-md-12 col-lg-12 col-xl-12 header p-2">
                        <h4>Hi There, Hope.</h4>
                        <h4>Trending Now</h4>
                    </div>
                </div>
            </div>
        </div>
        </>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { logout })(MainSection);
// export default MainSection;