import React from 'react';
import {Link} from 'react-router-dom';


//Importing scss
import '../sass/components/_sidebar.scss';

const Sidebar= ()=>{
    return(
        <>
        <div className="p-0 m-0 text-center side-cont">
            <ul className="ul p-0 m-0">
                <h2 className=" py-3"> 
                    <Link to="/">
                        <a className="title" href="">Ongaku</a> 
                    </Link>
                </h2>
                <h5 className="library my-5">library</h5>
                <li className="pb-4 pt-2">
                    <Link to="/">
                        <a href="#">Home</a>
                    </Link>    
                </li>
                <li className="py-4">
                    <Link to="/songs">
                        <a href="#" >Songs</a>
                    </Link>
                </li>
                <li className="py-4"><a href="#" >Albums</a></li>
                <li className="py-4">
                    <Link to='/artist'>
                        <a href="#" >Artists</a>
                    </Link>
                </li>
                <li className="py-4">
                    <Link to='/artist'>
                        <a href="#" >Newly Added</a>
                    </Link>
                </li>
            </ul>
        </div>
        </>
    );
};

export default Sidebar;